<template>
  <div class="user-contanier">
    <indexHeader :titleMaxWidth="clientWidth - 148">
      <div class="user-left" slot="header-left">
        我的
      </div>
    </indexHeader>
    <section class="user-wrapper">
      <card>
        <div class="user-info" @click="toInfo">
          <div class="user-avatar">
            <img v-if="userInfo.avatarUrl" :src="userInfo.avatarUrl" />
          </div>
          <div class="user-info-content">
            <div class="user-info-content-name">
              {{ userInfo.name }}
            </div>
            <div class="user-info-content-title">{{ userInfo.title }}</div>
          </div>
          <van-icon name="arrow" />
        </div>
      </card>

      <div class="function-list">
        <div
          class="function-item"
          v-for="(item, index) in settings"
          :key="index"
          @click="toPage(item)"
        >
          <div class="function-item-left">
            <i class="super-app-iconfont" :class="item.icons.outside"></i>
            <i class="super-app-iconfont light" :class="item.icons.inside"></i>
          </div>
          <div class="function-item-content">
            {{ item.name }}
          </div>
          <div class="function-item-right-text" v-if="item.value">
            {{ item.value }}
          </div>
          <van-icon name="arrow" />
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Card from '../../components/card';
import IndexHeader from '@src/components/index-header';
import ConfigService from '@services/config-service';
import { getUserContext } from '@yl-app-jssdk/context';
import RouterService from '@services/router-service';
import { getWebViewPath } from '@yl-app-jssdk/path';
import { mapState } from 'vuex';

export default {
  components: {
    Card,
    IndexHeader,
  },

  data() {
    return {
      settings: [],
      userInfo: {
        avatarUrl: '',
        name: '',
        title: '',
      },
      // isLoading: true
    };
  },

  computed: {
    ...mapState({
      clientWidth: state => state.app.global.clientWidth,
    }),
  },

  activated() {
    this.init();
  },

  methods: {
    init() {
      let user = getUserContext();
      console.log('user =====> ', user);
      this.userInfo.avatarUrl =
        getWebViewPath(user.avatarPath) || user.avatarUrl || '';
      this.userInfo.name =
        user.tenantInfo.tenantName ||
        user.realname ||
        user.nickname ||
        user.username ||
        user.phone ||
        '';
      this.userInfo.title = user.role;
      this.settings = ConfigService.getMyConfig();
    },

    toInfo() {
      this.$router.push({ name: 'userInfo' });
    },

    toPage(item) {
      // this.$router.push({ path: item.router });
      return RouterService.goto(item);
    },
  },
};
</script>

